/**
 * Define styles for component representation
 */
/* ----------  flex component styles ---------- */
.sg-example-flex--fixed-height .p-flex {
  height: 180px; }

.sg-example-flex .sg-example-item {
  padding: 0.25rem 5rem;
  text-align: center;
  color: #fff; }

.sg-example-flex .p-flex + .p-flex {
  margin-top: 0.5rem; }

.sg-example-flex [class*="p-flex--align-items-"] .p-flex__child:not(.p-flex__child--align-self-stretch):nth-child(1n) {
  height: 40px; }

.sg-example-flex [class*="p-flex--align-items-"] .p-flex__child:not(.p-flex__child--align-self-stretch):nth-child(2n) {
  height: 80px; }

.sg-example-flex [class*="p-flex--align-items-"] .p-flex__child:not(.p-flex__child--align-self-stretch):nth-child(3n) {
  height: 54px; }

.sg-example-flex .p-flex--align-items-baseline .p-flex__child {
  margin-top: 1.5rem; }

.sg-example-flex .p-flex__child:nth-child(1n) {
  background-color: lightskyblue; }

.sg-example-flex .p-flex__child:nth-child(2n) {
  background-color: deepskyblue; }

.sg-example-flex .p-flex__child:nth-child(3n) {
  background-color: dodgerblue; }

.sg-example-flex .p-flex__child:nth-child(4n) {
  background-color: royalblue; }

.sg-example-flex .p-flex__child--align-self-baseline {
  margin-top: 1.5rem; }

.sg-example-flex [class*="p-flex__child--width"] .sg-example-item {
  padding-left: 0.5rem;
  padding-right: 0.5rem; }

.sg-example-flex--gap .p-flex__child {
  background-color: transparent !important; }
  .sg-example-flex--gap .p-flex__child:nth-child(1n) p {
    background-color: lightskyblue; }
  .sg-example-flex--gap .p-flex__child:nth-child(2n) p {
    background-color: deepskyblue; }
  .sg-example-flex--gap .p-flex__child:nth-child(3n) p {
    background-color: dodgerblue; }
  .sg-example-flex--gap .p-flex__child:nth-child(4n) p {
    background-color: royalblue; }

/* ----------  spacing component styles ---------- */
.sg-example-spacing .sg-example-item {
  background-color: lightskyblue;
  width: fit-content; }
